import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { Button } from 'react-bootstrap';
import { CopyToClipboard } from '../common';

import { BrightcovePlayerProps, BrightcovePlayer } from '../brightcove-player';
import { BrightcovePlayerControls } from '../brightcove-player';

import { SPUtility } from '../../services/sharepoint';
import { BCPlayer } from '@brightcove/video-cloud-sdk';

export class MiniVideoEmbed extends React.Component<MiniVideoEmbedProps> {
    constructor(props: MiniVideoEmbedProps, context?: any) {
        super(props, context);
    }

    render() {
        const { bcPlayerProps, accountId, playerId, videoId, defaultVideoPlayerId, showCloseDialog, retrieveAllPlayers, onPlayerPropsChanged } = this.props;

        const playerPreviewUrl = `https://players.brightcove.net/${accountId}/${playerId}_default/index.html?videoId=${videoId}`;
        const embedCode = ReactDOMServer.renderToStaticMarkup(
            <BrightcovePlayer accountId={accountId} playerId={playerId} mode="Video" modeId={videoId} {...bcPlayerProps as BrightcovePlayerProps} />
        ).replace(/&amp;/g, '&').replace(/%3A/g, ':');

        const labelClass = 'col-xs-3 control-label small';
        const controlClass = 'col-xs-9';

        return (
            <>
                <div className="form-group">
                    <label className={labelClass}>Preview in browser</label>
                    <div className={controlClass + ' word-wrap'}>
                        <a href={playerPreviewUrl} target="_blank">{playerPreviewUrl}</a>
                    </div>
                </div>
                <BrightcovePlayerControls
                    accountId={accountId}
                    retrieveAllPlayers={retrieveAllPlayers}
                    defaultPlayerId={defaultVideoPlayerId}
                    labelClass={labelClass}
                    controlClass={controlClass}
                    formControlClass="input-sm small"
                    whClass="col-xs-6"
                    bcPlayerProps={bcPlayerProps}
                    onPlayerPropsChanged={onPlayerPropsChanged}
                />
                <div className="col-xs-12 small">
                    <div>Brightcove Snippet (Paste to Script Editor web part or publishing zone):</div>
                    <textarea id="embedCode" style={{ width: '100%', height: '300px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={() => null} />
                    <CopyToClipboard />
                </div>
                {showCloseDialog &&
                    <div className="col-xs-12 small" style={{ paddingTop: '5px' }}>
                        <Button className="pull-right" bsSize="small" onClick={() => SPUtility.closeParentDialog(false)}>Close Dialog</Button>
                    </div>
                }
            </>
        );
    }
}

export interface MiniVideoEmbedProps {
    bcPlayerProps: Partial<BrightcovePlayerProps>;
    accountId: string;
    playerId?: string;
    videoId: string;
    defaultVideoPlayerId: string;
    showCloseDialog: boolean;
    retrieveAllPlayers: (accountId: string) => Promise<BCPlayer[]>;
    onPlayerPropsChanged: (bcPlayerProps: Partial<BrightcovePlayerProps>) => void;
}


