import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import autobind from 'autobind-decorator';
import { CopyToClipboard } from '../common';

import { BrightcovePlayer, BrightcovePlayerProps, BrightcovePlayerControls, getDefaultBrightcovePlayerProps } from '../brightcove-player';

import { ConnectorContext } from '../../services/connector';
import { Account } from '../../services/sharepoint';

export class VideoEmbed extends React.Component<VideoEmbedProps, VideoEmbedState> {
    constructor(props: VideoEmbedProps, context?: any) {
        super(props, context);

        let bcPlayerProps = getDefaultBrightcovePlayerProps();
        bcPlayerProps.mode = 'Video';
        bcPlayerProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

        this.state = {
            bcPlayerProps
        };
    }

    @autobind
    handlePlayerPropsChanged(bcPlayerProps: Partial<BrightcovePlayerProps>) {
        this.setState({
            bcPlayerProps
        });
    }

    render() {
        const { connectorContext, account, videoId } = this.props;
        const { bcPlayerProps } = this.state;

        const playerPreviewUrl = `https://players.brightcove.net/${account.accountId}/${bcPlayerProps.playerId ? bcPlayerProps.playerId : 'default'}_default/index.html?videoId=${videoId}`;
        const embedCode = ReactDOMServer.renderToStaticMarkup(
            <BrightcovePlayer accountId={account.accountId} playerId={bcPlayerProps.playerId} mode="Video" modeId={videoId} {...bcPlayerProps as BrightcovePlayerProps} />
        ).replace(/&amp;/g, '&').replace(/%3A/g, ':');

        return (
            <>
                <div className="form-horizontal">
                    <div className="form-group">
                        <div>
                            <iframe
                                style={{ width: '100%', height: '400px' }}
                                src={`//players.brightcove.net/${account.accountId}/${bcPlayerProps.playerId ? bcPlayerProps.playerId : 'default'}_default/index.html?videoId=${videoId}`}
                            />
                        </div>
                    </div>
                    <div className="form-group">
                        <label>Preview in browser</label>
                        <div>
                            <a href={playerPreviewUrl} target="_blank">{playerPreviewUrl}</a>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-6">
                        <h4>Copy Code</h4>
                        <div>Copy the appropriate code below and paste it into your web page or app.</div>
                        <textarea id="embedCode" style={{ width: '100%', height: '300px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={() => null} />
                        <CopyToClipboard />
                    </div>
                    <div className="col-md-6">
                        Change Player Size
                        <BrightcovePlayerControls
                            accountId={connectorContext.selectedAccount && connectorContext.selectedAccount.accountId}
                            retrieveAllPlayers={connectorContext.retrieveAllPlayers}
                            defaultPlayerId={connectorContext.selectedAccount && connectorContext.selectedAccount.defaultVideoPlayerId}
                            bcPlayerProps={bcPlayerProps}
                            onPlayerPropsChanged={this.handlePlayerPropsChanged}
                        />
                    </div>
                </div>
            </>
        );
    }
}

export interface VideoEmbedProps {
    connectorContext: ConnectorContext;
    account: Account;
    videoId: string;
}

export interface VideoEmbedState {
    bcPlayerProps: Partial<BrightcovePlayerProps>;
}


